/* Общие стили */
.page-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: Arial, sans-serif;
  }
  
  /* Шапка */
  .header {
    background-color: #d40000; /* Красный цвет */
    color: white;
    padding: 15px 0;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
    margin: 0;
  }
  
  .main-nav {
    display: flex;
    gap: 20px;
  }
  
  .nav-button {
    /* базовые стили */
    background: none;
    border: none;
    color: white;
    cursor: pointer;
    padding: 8px 16px;
    font-size: 16px;
    transition: all 0.3s;
  }
  
  .nav-button:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
  
  .nav-button.active {
    background-color: rgba(255, 255, 255, 0.2);
    border-bottom: 2px solid white;
  }
  
  .auth-buttons {
    display: flex;
    gap: 15px;
  }
  
  .auth-button {
    background-color: white;
    color: #d40000;
    border: none;
    border-radius: 4px;
    padding: 8px 15px;
    font-size: 14px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s;
  }
  
  .auth-button:hover {
    background-color: #f0f0f0;
  }
  
  /* Основное содержимое */
  .main-content {
    flex: 1;
    background-color: white;
    padding: 40px 20px;
  }
  
  .company-info {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    padding: 20px;
  }
  
  .company-info h2 {
    color: #333;
    font-size: 22px;
    margin-bottom: 15px;
  }
  
  .company-info p {
    color: #555;
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 10px;
  }
  
  /* Подвал */
  .footer {
    background-color: #d40000; /* Красный цвет */
    color: white;
    padding: 20px 0;
  }
  
  .footer-content {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
    padding: 0 20px;
  }
  
  .footer h3 {
    margin-bottom: 10px;
    font-size: 18px;
  }
  
  .footer p {
    margin: 5px 0;
    font-size: 14px;
  }

  /* Стили для контейнера с изображением */
.main-image-container {
    display: flex;
    justify-content: center;
    margin: 40px 0;
    padding: 20px 0;
    background-color: #ffffff; /* Фон по желанию */
  }
  
  .main-image {
    max-width: 80%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Адаптивность для мобильных устройств */
  @media (max-width: 768px) {
    .main-image {
      max-width: 95%;
    }
  }