.auth-container {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  
  .auth-container h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .auth-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: #555;
  }
  
  .form-group input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
  }
  
  .error {
    color: #e74c3c;
    font-size: 14px;
    margin-top: 5px;
    display: block;
  }
  
  .auth-button {
    background-color: #b92929;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 10px;
    transition: background-color 0.3s;
  }
  
  .auth-button:hover {
    background-color: #b92929;
  }
  
  .auth-button.cancel {
    background-color: #7f8c8d;
    margin-left: 10px;
  }
  
  .auth-button.cancel:hover {
    background-color: #7f8c8d;
  }
  
  .auth-links {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .auth-link {
    color: #3498db;
    text-decoration: none;
    margin-top: 10px;
  }
  
  .auth-link:hover {
    text-decoration: underline;
  }
  
  .auth-buttons {
    display: flex;
    justify-content: space-between;
  }